.BottomBar {
    position: fixed;
    bottom: 0;
    height: 3em;
    width: 100%;
    background: black;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
}

.BottomBar .linkWrap {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 0.3em;
    padding: 0.3em 0.8em;
    margin: 0.5em 1em;
    background-color: white;
    line-height: 1.3em;
}

a {
    text-decoration: none;
}


